<template>
  <div v-if="!loading">
    <v-card
        class="mx-auto my-12"
        max-width="574"
    >
      <template slot="progress">
        <v-skeleton-loader></v-skeleton-loader>
      </template>

      <v-img
          height="520"
          src="https://i.pinimg.com/originals/71/81/e8/7181e84d50cb87fa4ab9a5a8ab613dbe.jpg"
      ></v-img>
      <v-card-title>"Test"</v-card-title>
      <v-avatar left>
        <v-icon>mdi-account-circle</v-icon>
      </v-avatar>
      <v-card-text>"Test"</v-card-text>
      <v-card-text>"Test"</v-card-text>
      <div v-if="checkout===false">
        <v-btn
          color="primary"
          depressed
          elevation="2"
          outlined
          plain
          @click=Payment()>Pay</v-btn>
      </div>
      <div v-if="checkout === true">
        <v-btn :href=preferenceLink>Pagar ahora</v-btn>
      </div>
    </v-card>

  </div>
</template>

<script>

import Product from "@/models/product";
import ProductService from "@/services/product.service"

export default {
  name: "product-card",
  props: {
    product: Object[Product],
  },
  data(){
    return {
      rating: 0,
      ratings: {},
      loading: true,
      checkout: false,
      preferenceLink: null,
    }
  },
  methods: {
    Payment(){
      ProductService.getCheckout().then(response => {
        console.log(response)
        this.checkout = true
        this.preferenceLink = response.data
      })
    }
  },
  mounted(){
    this.loading = false;
  }

}
</script>

